import React from 'react'
import PageTitle from './../shared/page-title';
import Paragraph from './../shared/paragraph';
import Comments from './../shared/comments';
import Link from './../shared/link';

export default class Page extends React.Component {
    render() {
        return (
            <div>
                <PageTitle text="Sorting algorithms visualization in Three.js"/>

                <Paragraph>
                    A small 3D visualization of some sorting algorithms, implemented in Three.js.
                    Nothing fancy, just a test to get the “feel” of the engine.
                </Paragraph>

                <Paragraph>
                    Press left mouse button and drag the cursor in order to rotate the scene.
                </Paragraph>

                <Paragraph>
                    <Link to="https://github.com/0xc0dec/threejs-sortvis">Source on Github</Link>
                </Paragraph>

                <iframe src="https://rawgit.com/0xc0dec/threejs-sortvis/master/index.html"
                        frameBorder="0" allowFullScreen="allowfullscreen" style={{ width: '100%', height: '800px' }}/>

                <Comments/>
            </div>
        );
    }
}



